ів України на думку користувачів TripAdvisor. У грудні 2016 року, готель отримав нагороду в номінації «Краща програма соціальної і корпоративної відповідальності» за версією International Hospitality Awards. У 2015 році готель «ALFAVITO» виграв звання «Кращий Бізнес-Готель 4 * України» за версією національної премії гостинності «Hospitality Awards–2015». Готель став володарем «Certificate of Exellence» і «Travellers ‘Choice» в номінації Нotels for Service і Top Hotels від Trip Advisor (2013 р., 2014 р., 2015 р., 2016 р., 2017 р., 2018.), а також заслужено, два роки поспіль, отримував нагороду «Кращий АРТ Готель Україна » (2012 р. і 2013 р.). «ALFAVITO» – перший український готель, який успішно пройшов атестацію Асоціації Бізнес Туризму (АБТ) і отримав сертифікат «Конференц і Бізнес Готель», а також став володарем знаку якості «Top Quality Hotel» від HRS.")])],1)])}
